.header{
 display: flex;
 justify-content: space-between;
 align-items: center;
 padding: 1rem;
 /* width: 100%;
 height: 100px;
 background-color: rgba(0,0,0,0.9); */
 position:fixed ;
 width: 100%;
 height: 90px;
 z-index: 10;
}
.header-bg{
  background-color: rgba(0,0,0,0.85);
  transition: 0.5s;
}

.nav-menu{
  display:flex;

}
.nav-menu li{
    padding:  0 1rem;
  
}
.nav-menu li a{
    font-size: 1.2rem;
    font-weight: 500;
    color: white;
}
h1{
  color: black;
}
h2{
  color: white;
}
.humburger{
  display: none;
}

@media screen and (max-width:640px){
 .nav-menu{
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
     background-color: rgba(0,0,0,0.9); 
    position: absolute;
    top: 0;
    left: -100%;
    z-index: -3;
  } 
  .nav-menu.active{
    left: 0;
  }
  .nav-menu li{
    padding: 1rem 0;

  }
  .nav-menu li a{
    font-size: 2rem;
  }

  .humburger{
    display: initial;
  }
}
@media screen and (max-width:360px){
  .nav-menu{
     flex-direction: column;
     justify-content: center;
     align-items: center;
     width: 100%;
     height: 100vh;
      background-color: rgba(0,0,0,0.9); 
     position: absolute;
     top: 0;
     left: -100%;
     z-index: -3;
   } 
   .nav-menu.active{
     left: 0;
   }
   .nav-menu li{
     padding: 1rem 0;
 
   }
   .nav-menu li a{
     font-size: 1rem;
   }
 
   .humburger{
     display: initial;
   }
   h1{
    color: black;
    font-size: 2rem;
  }
  h2{
    color: white;
    font-size: 2rem;
  }
 }